body {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  word-break: break-all;
  word-break: break-word;
  hyphens: auto;
}

image {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
}

.plus-minus {
  border-radius: 5px;
  border: solid 1px #dcdcdc;
}

.plus-minus__plus {
  display: inline-block;
  outline: none;
  font-size: 14px;
  font-size-line-height: 2.14;
}

.plus-minus__minus {
  display: inline-block;
  outline: none;
  font-size: 14px;
  font-size-line-height: 2.14;
}

.plus-minus__input {
  display: inline-block;
  font-size: 14px;
  font-size-line-height: 2.14;
  font-size-line-height-letter-spacing: 0.1px;
  color: #0f2841;
}

.block {
  display: flex;
}

.top {
  font-size: 14px;
}

.top:after {
  content: " ";
  display: table;
  clear: both;
}

.top__logo {
  float: left;
}

.top__container {
  display: flex;
  align-items: center;
  padding: 32px 0;
}

.top__nav {
  display: flex;
}

.top__links {
  display: flex;
}

.top__link {
  display: block;
  padding: 12px 15px;
}

.top__btn-links {
  display: flex;
}

.top__btn-link {
  display: block;
  width: 96px;
  padding: 12px 0;
  margin: 0 5px;
  border-radius: 5px;
  text-align: center;
  background-color: #0caafe;
}

.top__actions-list {
  display: flex;
}

.info__section {
  position: relative;
  flex-grow: 1;
  padding: 0 21px 21px;
  color: #ffffff;
}

.info__section:nth-child(1) {
  background-image: url(../img/products/boyler_info.png), linear-gradient(29deg, #0caafe, #2df0f2);
  background-position: top 44px right 44px, center center;
  background-repeat: no-repeat;
}

.info__section:nth-child(1) .info__img {
  top: 45px;
}

.info__section:nth-child(2) {
  background-image: linear-gradient(29deg, #00ab89, #3affd3);
  margin: 0 2px;
}

.info__section:nth-child(2) .info__img {
  top: 29px;
}

.info__section:nth-child(3) {
  background-image: linear-gradient(29deg, #ff881f, #ffcf3c);
}

.info__section:nth-child(3) .info__img {
  top: 14px;
}

.info__title {
  padding-top: 145px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.67;
  letter-spacing: -0.5px;
}

.info__img {
  position: absolute;
}

.info__know-more {
  font-size: 14px;
  letter-spacing: 0.1px;
}

.footer__icon {
  display: inline-block;
}

.footer__icon svg {
  fill: #efefef;
  height: 28px;
}

.footer__payment-icon {
  display: inline-block;
}

.footer__payment-icon svg {
  height: 24px;
}

