.container-inner {
    max-width: 1180px;
    margin: 0 auto; }

.row {
    display: flex;
    flex-wrap: wrap; }

.row>[class*="col"]:first-child {
    padding-left: 0; }

.row>[class*="col"]:last-child {
    padding-right: 0; }

.col {
    flex-grow: 1; }

.col1 {
    @include col(1,12); }
.col2 {
    @include col(2,12); }
.col3 {
    @include col(3,12); }
.col4 {
    @include col(4,12); }
.col5 {
    @include col(5,12); }
.col6 {
    @include col(6,12); }
.col7 {
    @include col(7,12); }
.col8 {
    @include col(8,12); }
.col9 {
    @include col(9,12); }
.col10 {
    @include col(10,12); }
.col11 {
    @include col(11,12); }
.col12 {
    @include col(12,12); }
