//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$familyOpensans: 'Open Sans';

@font-face {
	@include fontdefault(normal, 300, $familyOpensans);
	@include fontpath('Open Sans Light', 'OpenSans-Light', opensans-light); }

@font-face {
	@include fontdefault(italic, 300, $familyOpensans);
	@include fontpath('Open Sans Light Italic', 'OpenSans-LightItalic', opensans-lightitalic); }

@font-face {
	@include fontdefault(normal, 400, $familyOpensans);
	@include fontpath('Open Sans Regular', 'OpenSans-Regular', opensans-regular); }

@font-face {
	@include fontdefault(italic, 400, $familyOpensans);
	@include fontpath('Open Sans Italic', 'OpenSans-Italic', opensans-italic); }

@font-face {
	@include fontdefault(normal, 600, $familyOpensans);
	@include fontpath('Open Sans SemiBold', 'OpenSans-SemiBold', opensans-semibold); }

@font-face {
	@include fontdefault(italic, 600, $familyOpensans);
	@include fontpath('Open Sans SemiBold Italic', 'OpenSans-SemiBoldItalic', opensans-semibolditalic); }

@font-face {
	@include fontdefault(normal, 700, $familyOpensans);
	@include fontpath('Open Sans Bold', 'OpenSans-Bold', opensans-bold); }

@font-face {
	@include fontdefault(italic, 700, $familyOpensans);
	@include fontpath('Open Sans Bold Italic', 'OpenSans-BoldItalic', opensans-bolditalic); }

@font-face {
	@include fontdefault(normal, 800, $familyOpensans);
	@include fontpath('Open Sans ExtraBold', 'OpenSans-ExtraBold', opensans-extrabold); }
@font-face {
	@include fontdefault(italic, 800, $familyOpensans);
	@include fontpath('Open Sans ExtraBold Italic', 'OpenSans-ExtraBoldItalic', opensans-extrabolditalic); }

